.UserCog {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  &__return {
    width: 25px;
    height: 25px;
    border-bottom: 2px solid $font-color;
    border-left: 2px solid $font-color;
    position: absolute;
    top: 30px;
    left: 30px;
    transform: rotate(45deg);
  }
  &__image {
    width: 70%;
  }
}
